<template>
  <div class="restaurants page-wrapper">
    <app-top-links></app-top-links>
    <app-inner-page-hero></app-inner-page-hero>
    <app-result-show :total="total"></app-result-show>
    <section class="restaurants-page">
      <b-container>
          <b-row>
            <div class="col-xs-12 col-sm-5 col-md-5 col-lg-3">
              <app-widget-sidebar-filter></app-widget-sidebar-filter>
              <!--<app-widget-price-range></app-widget-price-range>-->
              <app-widget-popular-tags></app-widget-popular-tags>
            </div>
            <div class="col-xs-12 col-sm-7 col-md-7 col-lg-9">
              <app-restaurants-list v-for="restaurant in featuredRestaurants"
              :restaurant="restaurant" :key="restaurant.id"></app-restaurants-list>
            </div>
          </b-row>
      </b-container>
    </section>
    <app-how-get-apps></app-how-get-apps>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AppHowGetApps from '../components/root/HowGetApps'
import AppTopLinks from './partials/TopLinks'
import AppInnerPageHero from './partials/restaurants/InnerPageHero'
import AppResultShow from './partials/restaurants/ResultShow'
import AppWidgetSidebarFilter from './partials/restaurants/WidgetSidebarFilter'
import AppWidgetPriceRange from './partials/restaurants/WidgetPriceRange'
import AppWidgetPopularTags from './partials/WidgetPopularTags'
import AppRestaurantsList from './partials/restaurants/RestaurantsList'

export default {
  components: {
    AppTopLinks,
    AppInnerPageHero,
    AppResultShow,
    AppWidgetSidebarFilter,
    AppWidgetPriceRange,
    AppWidgetPopularTags,
    AppRestaurantsList,
    AppHowGetApps
  },
  data () {
    return {
      featuredRestaurants: [],
      total: 0,
      offset: 0
    }
  },
  computed: {
    ...mapGetters({
      currentPosition: 'currentPosition'
    })
  },
  methods: {
    async getFeaturedRestaurants () {
      let response = []

      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/latitude/' + this.currentPosition.lat + '/longitude/' + this.currentPosition.lng + '/offset/' + this.offset + '/limit/15'
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    arrayUnique: function (array) {
      let a = array.concat()
      for (let i = 0; i < a.length; ++i) {
        for (let j = i + 1; j < a.length; ++j) {
          if (a[i].id === a[j].id) {
            a.splice(j--, 1)
          }
        }
      }
      return a
    },
    infiniteHandler: function ($state) {
      this.busy = true
      let vm = this
      this.getFeaturedRestaurants().then((response) => {
        return response.data
      }).then(response => {
        setTimeout(() => {
          if (response && response.data) {
            vm.featuredRestaurants = vm.arrayUnique(vm.featuredRestaurants.concat(response.data))
          }
          vm.total = vm.featuredRestaurants.length
          $state.loaded()
        }, 1000)
      })
      console.log('Load More hehe')
    }
  }
}
</script>

<style scoped>

</style>



