<template>
    <b-nav-item-dropdown id="login-dropdown" right>
        <!-- Using button-content slot -->
        <template slot="button-content">
        <b>Entrar ou cadastrar</b>
        </template>
        <!-- <b-dropdown-item> -->
        <div class="login-dropdown-content">
            <b-row>
                <div class="col-md-12">
                    <h4 align="center" class="pb-1">Entrar com E-mail</h4>
                    <b-form>
                        <div class="form-group">      
                            <b-form-input class="form-control mr-sm-2" type="email" placeholder="seuemail@exemplo.com" required v-model="user.email"></b-form-input>                   
                        </div>
                        <div class="form-group">
                            <b-form-input class="form-control mr-sm-2" type="password" placeholder="Senha" required v-model="user.password"></b-form-input>
                        </div>
                        <b-form-checkbox><div class="mb-2 mx-auto" style="width: 200px;">Mantenha-me conectado</div></b-form-checkbox>
                        <div class="form-group">
                            <b-button class="btn btn-theme btn-block" @click="login()"><i class="fa fa-sign-in mr-2"></i>Entrar</b-button>
                        </div>    
                    </b-form>
                    <p align="center">ou</p>
                    <div class="social-buttons">
                        <facebook-login class="btn-fb"
                          appId="1677974845578085"
                          loginLabel="Entrar com Facebook"
                          logoutLabel="Sair"
                          @login="facebookLogin"
                          @logout="onLogout">
                        </facebook-login>
                    </div>
                   </div>
                <div class="bottom text-center">
                    Novo aqui ?
                    <b-link to="cadastro"><b>Junte-se a nós!</b></b-link>
                </div>
                <div class="bottom text-center">
                    Esqueceu a senha ?
                    <b-link to="/"><b>Recuperar</b></b-link>
                </div>
            </b-row>
        </div>
    </b-nav-item-dropdown>
</template>

<script>
import facebookLogin from 'facebook-login-vuejs'
export default {
  data () {
    return {
      user: {
        email: '',
        password: ''
      },
      FB: undefined
    }
  },
  methods: {
    login () {
      this.$store.dispatch('login', {
        email: this.user.email,
        password: this.user.password
      }).then((data) => {
        if (data.status) {
          this.$router.push('/restaurantes')
        } else {
          this.$toasted.error(data.message).goAway(3000)
        }
      })
    },
    socialLogin (id, email) {
      this.$store.dispatch('socialLogin', {
        email: email
      }).then((data) => {
        console.log(data)
      })
    },
    facebookLogin (data) {
      this.FB.api('/me', 'GET', { fields: 'id,name,email' },
        userInformation => {
          this.socialLogin(userInformation.id, userInformation.email)
        }
      )
    },
    onLogout () {
      this.isConnected = false
    },
    onLogin () {
      this.facebookLogin()
    }
  },
  components: {
    facebookLogin
  }
}
</script>

<style scoped>
.navbar-light .navbar-nav .nav-link {
    color: black;
}

.login-dropdown-content {
    min-width: 250px;
    padding: 14px 14px 0;
    overflow: hidden;
}

.login-dropdown-content .bottom {
    border-top: 1px solid #ddd;
    clear: both;
    padding: 14px;
    width: 100%;
}

.login-dropdown-content .social-buttons {
    margin: 12px 0;
}

.login-dropdown-content .social-buttons button {
    width: 100%;
}

.login-dropdown-content .form-group {
    margin-bottom: 10px;
}

.btn-fb {
    color: #fff;
    background-image: linear-gradient(#4C69BA, #3B55A0);
    width: 255px;
    height: 38px;
}

.btn-fb:hover {
    color: #fff;    
    background-image: linear-gradient(#4C69BA, #3B55A0);
}

@media(max-width:768px) {
    .login-dropdown-content {
        background-color: inherit;
        color: black;
    }
    .login-dropdown-content .bottom {
        background-color: inherit;
        border-top: 0 none;
    }
}
</style>



