<template>
    <!-- start: Inner page hero -->
    <section class="inner-page-hero bg-image">
        <div class="profile">
            <b-container>
                <b-row>
                  <b-col cols="12" md="3" class="profile-img">
                        <div class="image-wrap">
                            <figure>
                                <img :src="restaurant.img" alt="Restaurant Logo">
                            </figure>
                        </div>
                    </b-col>
                    <b-col cols="12" md="6" class="profile-desc">
                        <div class="pull-left right-text white-txt">
                            <h6>{{restaurant.name}}</h6>
                            <b-btn variant="success" class="my-3" v-if="restaurant.open == 1">ABERTO</b-btn>
                            <b-btn variant="danger" class="my-3" v-if="restaurant.open == 0">FECHADO</b-btn>
                            <p><i class="fa fa-cutlery"></i> {{restaurant.Categories | category}}</p>
                            <ul class="nav nav-inline">
                                <li class="nav-item">
                                    <a class="nav-link active" href="#">
                                        <i class="fa fa-motorcycle"></i> {{restaurant.delivery_min}} · {{restaurant.delivery_max}}
                                    </a>
                                </li>
                                <li class="nav-item ratings">
                                    <a class="nav-link" href="#">
                                      <span v-if="restaurant.stars != null">
                                          <i v-for="(n) in Math.round(restaurant.stars)" :key="n" class="fa fa-star"></i>
                                              <span v-if="Math.round(restaurant.stars) < 5">
                                                  <i v-for="(i) in (5 - Math.round(restaurant.stars))" :key="i" class="fa fa-star-o"></i>
                                              </span>
                                      </span>
                                      <span v-else-if="restaurant.stars == null">
                                          <p style="color: #ffb42d;"><strong>NOVO!</strong> <i class="fa fa-fire ml-1"></i></p>
                                      </span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </b-col>
                </b-row>
            </b-container>
        </div>
    </section>
    <!-- end:Inner page hero -->
</template>

<script>
export default {
  props: ['restaurant'],
  filters: {
    category (categories) {
      return categories.map(category => category.name).join(' · ')
    }
  }
}
</script>

<style scoped>
/* ==========================
*  Inner pages bg-image resolution: 1670x480
============================*/
.bg-image {
    background: url(http://healtheatingfood.com/wp-content/uploads/2016/04/functional-food.jpg);
    background-size: cover;
}

.inner-page-hero {
    padding-top: 5%;
    padding-bottom: 5%;
}


/********* profile banner part ***********/

.profile-banner figure img {
    width: 100%;
}

.image-wrap {
    border-radius: 2px;
    padding: 10px;
    float: left;
    background: #fff;
}

.image-wrap figure {
    background: #FFF;
    border-radius: 2px;
    display: block;
    overflow: hidden;
    float: left;
    margin-bottom: 0;
}

.image-wrap figure img {
    max-width: 100%;
    display: block;
}

.profile p {
    color: #fff;
}

.banner figure {
    margin-bottom: 0;
}

.profile-desc h6 {
    font-size: 26px;
    line-height: 31px;
    color: #fff;
}

/* .profile .right-text {} */

.profile-img {
    width: 24.7%;
}

.profile .right-text span {
    margin-bottom: 8px;
    display: block;
}

.profile-desc h6 {
    display: inline-block;
    margin-right: 3rem;
    margin-bottom: 10px;
}

.profile-desc a.btn {
    display: inline-block;
    padding: 3px 20px;
    background-color: #82b21c;
    vertical-align: text-bottom;
    font-size: 13px;
    color: #fff;
}

.profile-desc .right-text {
    width: 100%;
    border-right: 0;
    color: rgba(255, 255, 255, .5);
    margin-top: 30px;
}

.profile-desc .nav-link {
    padding-top: 0;
    padding-right: 1rem;
    padding-bottom: 0.5rem;
    padding-left: 0;
}

.profile-desc .nav-item a {
    color: #fff;
}

.profile-desc .nav-item.ratings a span i {
    color: #ffd953;
    font-size: 16px;
}

@media (min-width:320px) and (max-width:768px) {
    .profile-desc .right-text {
        margin-top: 0;
    }
    .profile-desc h6 {
        margin-right: 0;
    }
    .image-wrap {
        padding: 1px;
        border-radius: 2px;
        margin-bottom: 20px;
    }
    .profile-desc a.btn {
        float: right;
    }
    .image-wrap img {
        width: auto;
    }
    .profile-img {
        width: 100%;
    }
}

@media (min-width: 768px) and (max-width: 991px) {
    .profile-desc .right-text {
        margin-top: 0;
    }
}
</style>



