<template>
    <div>
        <b-form inline class="text-center" v-if="showCEP">
            <b-container>
                <b-row>
                    <b-col>
                        <the-mask mask="#####-###" class="mb-2 mr-sm-2 mb-sm-0 form-control form-control-lg" type="text" placeholder="Digite seu CEP" v-model="address.cep"></the-mask>
                        <b-button variant="theme" class="btn btn-lg" @click="searchAddress()">Buscar</b-button>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col class="pr-5 mr-5">
                        <b-form-text class="mx-4">
                            <b-link @click="invertSearch()">
                                <u>Não sabe o CEP? Digite seu endereço</u>
                            </b-link>
                        </b-form-text>
                    </b-col>
                </b-row>
            </b-container>
        </b-form>

        <b-form v-if="showAddress" class="search-address">
            <b-container>
                <b-row align-h="center" class="text-center">
                    <b-col cols="12" sm="6">
                        <h3 class="search-address-title">
                            <strong>Informe o endereço de entrega!</strong>
                        </h3>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="12" sm="2" class="pr-2">
                        <b-select v-model="address.uf" :options="listStates" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </b-col>
                    <b-col cols="12" sm="6" class="pl-0">
                        <b-select v-model="address.city" :options="listCities(address.uf)" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="12" sm="6" class="pr-2">
                        <b-form-input type="text" placeholder="Digite seu endereço" v-model="address.street" size="lg" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-form-input>
                    </b-col>
                    <b-col cols="12" sm="2" class="pl-0">
                        <b-form-input type="text" placeholder="Nº" v-model="address.number" size="lg"></b-form-input>
                    </b-col>
                </b-row>
                <b-row align-h="center" class="mt-2">
                    <b-col cols="12" sm="2" class="pr-2">
                        <b-btn variant="dark" style="width: 100%" size="lg" @click="invertSearch()">Voltar</b-btn>
                    </b-col>
                    <b-col cols="12" sm="6" class="pl-0">
                        <b-btn variant="theme" style="width: 100%" size="lg" @click="setAddress()">Buscar</b-btn>
                    </b-col>
                </b-row>
            </b-container>
        </b-form>
    </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import {TheMask} from 'vue-the-mask'
export default {
  data () {
    return {
      disableFields: true,
      showCEP: true,
      showAddress: false,
      address: {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        street: '',
        number: ''
      },
      ...mapState(['lastUsed'])
    }
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF'
    //   address: this.lastUsed
    })
  },
  methods: {
    invertFields () {
      this.disableFields = !this.disableFields
    },
    invertSearch () {
      this.invertFields()
      this.showCEP = !this.showCEP
      this.showAddress = !this.showAddress
    },
    async searchAddress () {
      let _axios = this.$http.create()
      let cep = this.address.cep
      let response = await _axios.get(
        'correios/cep/' + cep
      )
      let _address = response.data.data
      this.address = {
        cep: _address.cep,
        uf: _address.uf,
        city: { name: _address.localidade, uf: _address.uf },
        street: _address.logradouro,
        number: ''
      }
      this.invertSearch()
    },
    setAddress () {}
  },
  created () {},
  components: {TheMask}
}
</script>

<style scoped>
.search-address .disabled {
    background: #cecece;
    -webkit-appearance: none;
    opacity: 1;
}

.search-address-title {
    font-family: "Give You Glory", cursive;
    color: #fff;
}
</style>




