<template>
    <section class="contact-page inner-page">
        <div class="container">
            <div class="row">
                <!-- REGISTER -->
                <div class="col-md-8">
                    <div class="widget">
                        <div class="widget-body">
                            <form>
                                <div class="row">
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Nome</label>
                                        <input class="form-control" type="text" id="example-text-input" v-model="user.firstName">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Sobrenome</label>
                                        <input class="form-control" type="text" id="example-text-input-2" v-model="user.lastName">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Email</label>
                                        <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Email" v-model="user.email">
                                        <small id="emailHelp" class="form-text text-muted">Nós nunca compartilharemos seu email com ninguém.</small>
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Telefone</label>
                                        <input class="form-control" type="tel" value="(34) 95555-5555" id="example-tel-input-3" v-model="user.telephone">
                                        <small class="form-text text-muted">Nós nunca compartilharemos seu telefone com ninguém.</small>
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Senha</label>
                                        <input type="password" class="form-control" id="exampleInputPassword1" v-model="user.password">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Confirmar Senha</label>
                                        <input type="password" class="form-control" id="exampleInputPassword2" v-model="confirmPassword">
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-sm-4">
                                        <p>
                                            <b-link class="btn btn-theme" @click="register()">Registrar</b-link>
                                        </p>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <!-- end: Widget -->
                    </div>
                    <!-- /REGISTER -->
                </div>
                <!-- WHY? -->
                <div class="col-md-4">
                    <h4>O registro é rápido, fácil e gratuito.</h4>
                    <p>Uma vez que você está registrado, você pode:</p>
                    <hr>
                    <img src="http://placehold.it/400x300" alt="Depois de registrado" class="img-fluid">
                    <p></p>
                    <div role="tablist">
                        <b-card no-body class="mb-1">
                            <b-card-header header-tag="header" class="p-1" role="tab">
                                <b-link v-b-toggle.accordion1>{{ question }}</b-link>
                                <!-- <b-btn block href="#" v-b-toggle.accordion1 variant="info"></b-btn> -->
                            </b-card-header>
                            <b-collapse id="accordion1" accordion="my-accordion" role="tabpanel">
                                <b-card-body>
                                    <p class="card-text">
                                        {{ answer }}
                                    </p>
                                </b-card-body>
                            </b-collapse>
                        </b-card>
                    </div>
                    <!-- end:Panel -->
                    <h4 class="m-t-20">Entre em contato com o suporte ao cliente.</h4>
                    <p> Se você está procurando por mais ajuda ou tenha uma pergunta a fazer, por favor </p>
                    <p>
                        <b-link to="contact" class="btn btn-theme m-t-15">Contate-nos</b-link>
                    </p>
                </div>
                <!-- /WHY? -->
            </div>
        </div>
    </section>
</template>

<script>
export default {
  data () {
    return {
      question: 'Preciso ser registrado para dar uma olhada nos restaurantes e menus?',
      answer: 'Não precisa, basta selecionar seu endereço e navegar nos mais variados restaurantes e pratos a sua disposição...',
      user: {},
      confirmPassword: ''
    }
  },
  methods: {
    register () {
      this.$http.post('/users', {
        user: this.user
      }).then((response) => {
        this.$store.dispatch('login', {
          user: this.user
        }).then(() => {
          this.$router.push('/restaurants')
        })
      })
    }
  }
}
</script>

<style scoped>
.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
    margin-bottom: 20px;
}

.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 2px;
    position: relative;
}

.widget-body {
    padding: 20px;
}
</style>



