<template>
  <headroom :classes="classes" :zIndex="1050">
    <header id="header">
      <b-navbar toggleable="md" type="light" variant="faded">
        <div class="container-fluid">
          <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

          <b-navbar-brand to="/">
            <b-img rounded src="../../../assets/logo-saciafome.png" alt="SaciaFome Logo" />
          </b-navbar-brand>

          <b-collapse is-nav id="nav_collapse">

            <b-navbar-nav>
              <b-nav-item  v-if="lastUsed.city != null">
               <span class="location">
                <i class="fa fa-map-marker mr-2"></i>
                {{lastUsed.city}}
                </span>
              </b-nav-item>
            </b-navbar-nav>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">
              <app-logged-dropdown v-if="isLoggedIn"></app-logged-dropdown>
              <app-login-dropdown v-else-if="!isLoggedIn"></app-login-dropdown>
            </b-navbar-nav>

          </b-collapse>
        </div>
      </b-navbar>
    </header>
  </headroom>
<!-- navbar.vue -->
</template>

<script>
import { mapGetters } from 'vuex'
import { headroom } from 'vue-headroom'
import AppLoggedDropdown from './login/LoggedDropdown'
import AppLoginDropdown from './login/LoginDropdown'

export default {
  data () {
    return {
      classes: {
        initial: '',
        pinned: 'fade show',
        unpinned: 'fade'
      }
    }
  },
  components: {
    headroom,
    AppLoggedDropdown,
    AppLoginDropdown
  },
  computed: {
    ...mapGetters({
      isLoggedIn: 'isLoggedIn',
      lastUsed: 'lastUsed'
    })
  }
}
</script>

<style scoped>
#header {
    position: fixed;
    width: 100%;
    border: 0;
    z-index: 99;
}

.headroom--pinned {
    display: block;
}

.headroom--unpinned {
    display: none;
}

.navbar {
    background-image: none;
    background-color: rgba(255, 180, 45, 0.8);
    padding: 0rem 1rem;
    border-radius: 0;
}

.container {
    max-width: 1270px;
    width: 94%;
}

.nav-link-custom {
  color: black !important;
}

.location {
  font-size: 20px;
  color: white;
}
</style>



