import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import VueAxios from 'vue-axios'
import axios from 'axios'
import { sync } from 'vuex-router-sync'
import 'font-awesome/css/font-awesome.css'
import '@/assets/css/style.css'
import vSelect from 'vue-select'
import VueCarousel from 'vue-carousel'
import App from './App'
import store from './store'
import router from './router'
import BackToTop from '@/components/root/BackToTop/main'

// register the plugin on vue
import Toasted from 'vue-toasted'

Vue.use(Toasted)
Vue.use(BootstrapVue)
Vue.use(VueAxios, axios)
Vue.component('v-select', vSelect)
Vue.use(VueCarousel)
Vue.use(BackToTop)

Vue.config.productionTip = process.env.NODE_ENV === 'production'

Vue.axios.defaults.baseURL = process.env.BASE_URL

if (!(localStorage.getItem('token'))) {
  localStorage.setItem('token', process.env.TMP_TOKEN)
}

sync(store, router)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})

function stopRKey (evt) {
  let _evt = evt || (event || null)
  let node = _evt.target || (_evt.srcElement || null)
  return !((_evt.keyCode === 13) && (node.type === 'text'))
}

document.onkeypress = stopRKey



