<template>
<b-nav-item-dropdown id="logged-dropdown" right>
    <!-- Using button-content slot -->
    <template slot="button-content">
        <em>Bem-vindo(a), <b>{{ user.first_name }}</b></em>
    </template>
    <b-dropdown-item href="/minha-conta">Perfil</b-dropdown-item>
    <b-dropdown-item href="/enderecos">Meus endereços</b-dropdown-item>
    <b-dropdown-item href="#">Favoritos</b-dropdown-item>
    <b-dropdown-item href="#">Ajuda</b-dropdown-item>
    <b-dropdown-item @click="logout()">Sair</b-dropdown-item>
</b-nav-item-dropdown>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF',
      user: 'user'
    })
  },
  methods: {
    logout () {
      this.$store.dispatch('logout').then(() => {
        this.$router.push({name: 'home'})
      })
    }
  }
}
</script>

<style scoped>
</style>



