<template>
    <div class="sidebar clearfix m-b-20">
        <div class="main-block">
            <div class="sidebar-title white-txt">
                <h6>Escolha a categoria</h6>
                <i class="fa fa-cutlery pull-right"></i>
            </div>
            <div class="input-group">
                <!-- <input type="text" class="form-control search-field" placeholder="Procure sua comida favorita"> -->
                <!-- <span class="input-group-btn">
                    <button class="btn btn-secondary search-btn" type="button">
                        <i class="fa fa-search"></i>
                    </button>
                </span> -->
            </div>
            <b-row>
            <b-col lg="6" xs="6" sm="12" md="6" class="mx-3 my-2">
            <b-form>
                <b-form-checkbox-group v-model="selected" name="category-checkbox" :options="categories"></b-form-checkbox-group>
            </b-form>
            </b-col>
            </b-row>
            <div class="clearfix"></div>
        </div>
        <!-- end:Sidebar nav -->
        <!-- <div class="widget-delivery">
                <b-form-radio-group id="radios2" name="radioSubComponent2">
                <div class="container-fluid">
                    <div class="row">
                        <div class="col-xs-6 col-sm-12 col-md-6 col-lg-6">                        
                            <b-form-radio value="Delivery">Entrega</b-form-radio>
                        </div>
                        <div class="col-xs-6 col-sm-12 col-md-6 col-lg-6">
                            <b-form-radio value="Takeover">Retirada</b-form-radio>
                        </div>
                    </div>
                </div>
            </b-form-radio-group>
        </div> -->
    </div>
</template>

<script>
export default {
  data () {
    return {
      selected: [],
      categories: []
    }
  },
  methods: {
    async getCategories () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/categories'
        )
      } catch (e) {
        console.log(e)
      }
      let novo = response.data.data.map(item => [item.name])
      this.categories = novo
    }
  },
  created () {
    this.getCategories()
  }
}
</script>

<style scoped>
.sidebar {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
}

.sidebar-inner {
    padding-left: 15px;
    padding-right: 15px;
}

.sidebar-inner .sidebar input,
.sidebar .input-group-btn .btn {
    height: 55px;
    border-left: 0;
    border-right: 0;
}

.sidebar-title {
    padding: 15px 15px 15px;
    background: #ffb42d;
    float: left;
    width: 100%;
}

.sidebar-title h6 {
    float: left;
    margin-bottom: 0;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
}

.sidebar-title i {
    float: right;
    color: #fff;
    font-size: 15px;
    margin-top: 3px;
}

.main-block ul li {
    line-height: 28px;
    font-weight: 300;
    display: block;
    border-bottom: 1px solid #eaebeb;
    font-weight: 500;
    color: #414551;
}

.main-block ul li a {
    color: #414551;
    display: block;
    font-weight: 500;
    padding: 15px;
}

.main-block ul {
    float: left;
    width: 100%;
    margin-bottom: 0;
}

.main-block ul li:hover {
    background: #fffdfd;
}

.main-block ul li:hover a {
    color: #ffb42d
}

.main-block ul:hover li:last-child {
    border-bottom: 1px solid transparent;
    border-bottom: 1px solid #eaebeb;
}

.main-block ul li:first-of-type {
    border-top: none
}

.main-block form ul li {
    padding: 15px
}

.main-block .input-group input,
.main-block .input-group-btn {
    height: 55px;
    border-left: none
}

.widget-delivery .col-xs-6 {
    padding: 25px 15px 20px;
    background: #fff;
    border-right: 1px solid #eaebeb;
    display: -webkit-inline-box;
    border-bottom: 1px solid #eaebeb;
}

.widget-delivery .col-xs-6:last-of-type {
    border-right: 1px solid transparent;
}

.sidebar .widget-delivery .col-xs-6 {
    border-bottom: none
}
</style>


